// src/App.js
import { useEffect, useState } from "react";
import { BrowserRouter, Link, Route, Routes } from "react-router-dom";

/* --------- Petits crans et styles simples --------- */
const shell = {
  page: { maxWidth: 940, margin: "24px auto", padding: "0 16px", color:"#e9eefb" },
  header: {
    display:"flex", flexWrap:"wrap", gap:12, alignItems:"center",
    padding:"14px 18px", background:"#1a1f36"
  },
  a: { color:"#b9c4ff", textDecoration:"none", fontWeight:600 },
  badge: s => ({ marginLeft:8, fontSize:12, padding:"2px 8px",
    borderRadius:12, background: s==="ok"?"#193c2e":"#3b1d1d",
    color: s==="ok"?"#2bd67b":"#ff6b6b"
  }),
  card:{ background:"#11162a", border:"1px solid #2a3358",
         borderRadius:10, padding:18, marginTop:16 }
};

/* --------- Entte avec healthcheck --------- */
function TopBar(){
  const [health,setHealth] = useState(null);

  useEffect(()=>{
    let stop = false;
    (async ()=>{
      try{
        const r = await fetch("/api/lia/health");
        const j = await r.json();
        if(!stop) setHealth({ ok: !!j.ok, status:j.status||"running" });
      }catch{
        if(!stop) setHealth({ ok:false, status:"down" });
      }
    })();
    return ()=>{ stop = true; };
  },[]);

  return (
    <header style={shell.header}>
      <Link to="/" style={shell.a}>Accueil</Link>
      <Link to="/connexion" style={shell.a}>Connexion</Link>
      <Link to="/inscription" style={shell.a}>Inscription</Link>
      <Link to="/motdepasse" style={shell.a}>Mot de passe</Link>
      <Link to="/profile" style={shell.a}>Profil</Link>
      <Link to="/dashboard" style={shell.a}>Dashboard</Link>
      <Link to="/proprietaire" style={shell.a}>Propritaire</Link>
      <Link to="/locataire" style={shell.a}>Locataire</Link>
      <Link to="/menage" style={shell.a}>Mnage</Link>
      <Link to="/entretien" style={shell.a}>Entretien</Link>
      <Link to="/succession" style={shell.a}>Succession</Link>
      <Link to="/viager" style={shell.a}>Viager</Link>
      <Link to="/superadmin" style={shell.a}>SuperAdmin</Link>
      <span style={{marginLeft:"auto"}}>
        Sant API:
        <span style={shell.badge(health?.ok ? "ok" : "ko")}>
          {health ? (health.ok ? "OK" : "KO") : ""}
        </span>
      </span>
    </header>
  );
}

/* --------- Pages trs simples (placeholders) --------- */
function Page({title}){ 
  return (
    <div style={shell.page}>
      <h1>{title}</h1>
      <div style={shell.card}>
        <p>Contenu <b>{title}</b>   raccorder  tes modules existants.</p>
        <p>Si tu avais des fichiers/sections, on les rintgrera ici.</p>
      </div>
    </div>
  );
}

/* --------- App principale avec routes --------- */
export default function App(){
  return (
    <BrowserRouter>
      <div style={{background:"#0f1220", minHeight:"100vh"}}>
        <TopBar/>
        <Routes>
          <Route path="/"            element={<Page title="Accueil" />} />
          <Route path="/connexion"   element={<Page title="Connexion" />} />
          <Route path="/inscription" element={<Page title="Cration de compte" />} />
          <Route path="/motdepasse"  element={<Page title="Mot de passe oubli" />} />
          <Route path="/profile"     element={<Page title="Profil" />} />
          <Route path="/dashboard"   element={<Page title="Tableau de bord" />} />
          <Route path="/proprietaire" element={<Page title="Propritaire" />} />
          <Route path="/locataire"    element={<Page title="Locataire" />} />
          <Route path="/menage"       element={<Page title="Mnage" />} />
          <Route path="/entretien"    element={<Page title="Entretien" />} />
          <Route path="/succession"   element={<Page title="Succession" />} />
          <Route path="/viager"       element={<Page title="Viager" />} />
          <Route path="/superadmin"   element={<Page title="SuperAdmin" />} />
          <Route path="*"             element={<Page title="404  Page introuvable" />} />
        </Routes>
      </div>
    </BrowserRouter>
  );
}
